<template><section><h1>CommonSelect</h1>
<p>下拉框组件</p>
<p>个人认为很多组件都会有其相似性的，是可以抽象出来一致性的东西的。例如下拉框与tab切换应该是有着几乎一样的api思想及逻辑。
所以基于上面的想法，我认为——特别是数据查询和报表页面爆了重复查询ajax请求的bug——很多事情（逻辑）是不应该交给子组件来做的。如果本身自己能够完成的情况下。
所以基于旧版，去掉了修改options会导致事件触发的情况</p>
<hr>
<h3>基础用法</h3>

<div class="demo-block">
  <common-select @change="chgMenu" :options="options" :selected="selected" :label="label">
  </common-select>
</div>
<demo-block>
                        <div slot="desc"><html><head>

</head><body><div class="demo-block">
  <common-select @change="chgMenu" :options="options" :selected="selected" :label="label">
  </common-select>
</div>
</body></html></div>
