<h3>Attributes</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>options</td>
<td>下拉选项。形如：[{label: '菜单标题', value: '该标题的value值，唯一'}]</td>
<td>Array</td>
<td>必填</td>
<td>[]</td>
</tr>
<tr>
<td>selected</td>
<td>选中值。形如：['选中value1', '选中value2']</td>
<td>Array</td>
<td>—</td>
<td>[]</td>
</tr>
<tr>
<td>label</td>
<td>提示文本。形如：'请选择基金经理'</td>
<td>String</td>
<td>—</td>
<td>''</td>
</tr>
<tr>
<td>placeholder</td>
<td>下拉选项为空时的文本。形如：'暂无选项'</td>
<td>String</td>
<td>—</td>
<td>'暂无选项'</td>
</tr>
<tr>
<td>column_num</td>
<td>下拉框内选项列数。</td>
<td>Number</td>
<td>—</td>
<td>5</td>
</tr>
<tr>
<td>multiable</td>
<td>是否支持多选。</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>searchable</td>
<td>是否支持检索</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>classname</td>
<td>组件外层样式</td>
<td>String</td>
<td>—</td>
<td>''</td>
</tr>
</tbody>
</table>
<h3>Slot</h3>
<p>无</p>
<h3>Events</h3>
<table class="table"><thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>change</td>
<td>用户点击修改后的回调</td>
<td>selected，由入参options数组中某一项或多项的value值组成的数组，表示选中该选项</td>
</tr>
</tbody>
</table>
</section></template>

<script>
  export default {
    data() {
      return {
        label: '仅支持单选',
        options: [{
          label:'待审批指令',
          value:'wait',
