<template><section>
<h1>message 消息提示</h1>
<hr>
<h2>基本用法 【默认3秒关闭】</h2>
<div class="demo-block">
  <xc-button type="text" @click="openLoading_1">点击显示 message</xc-button>
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><xc-button type="text" @click="openLoading_1">点击显示 message</xc-button>

  
</body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html">  <span class="hljs-tag">&lt;<span class="hljs-name">xc-button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"text"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"openLoading_1"</span>&gt;</span>点击显示 message<span class="hljs-tag">&lt;/<span class="hljs-name">xc-button</span>&gt;</span>

  <span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
      <span class="hljs-attr">methods</span>: {
        openLoading_1() {
          <span class="hljs-keyword">this</span>.$message(<span class="hljs-string">'hello'</span>);
        }
      }
    }
  </span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2>自定义关闭时间，设置 duration 属性</h2>
<div class="demo-block">
  <xc-button type="text" @click="openLoading_2">点击显示 message</xc-button>
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><xc-button type="text" @click="openLoading_2">点击显示 message</xc-button>

  
</body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html">  <span class="hljs-tag">&lt;<span class="hljs-name">xc-button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"text"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"openLoading_2"</span>&gt;</span>点击显示 message<span class="hljs-tag">&lt;/<span class="hljs-name">xc-button</span>&gt;</span>

  <span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
      <span class="hljs-attr">methods</span>: {
        openLoading_2() {
          <span class="hljs-keyword">this</span>.$message({
            <span class="hljs-attr">text</span>: <span class="hljs-string">'我要6秒'</span>,
            <span class="hljs-attr">duration</span>: <span class="hljs-number">6000</span>
          });
        }
      }
    }
  </span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
</section></template>

<script>
  export default {
    methods: {
      openLoading_1() {
        this.$message('hello');
      },
      openLoading_2() {
        this.$message({
          text: '我要6秒',
          duration: 6000
        });
      }
    }
  }
</script>


