<div class="demo-block">
  <p>选择的股票ID: {{ stockId }}</p>
  <p>选择的股票名称：{{ stockName }}</p>
<p><vue-stock-search style="width: 300px; height: 36px;" placeholder="请输入股票代码或名称" assets-class="e,ehksz,ehksh" stock-fullname="all" :has-loading-icon="false" cur-market="marketA" @stock_id="chioceStockId($event)" @stock_name="chioceStockName($event)"></vue-stock-search></p>
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><div class="demo-block">
