</div></demo-block>
<h2>stockSearch Attributes</h2>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
<th>ps</th>
</tr>
</thead>
<tbody>
<tr>
<td>placeholder</td>
<td>默认占位符</td>
<td>String</td>
<td>—</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>disabled</td>
<td>是否禁用</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
<td>—</td>
</tr>
<tr>
<td>assetsClass</td>
<td>查询的股票类型</td>
<td>String</td>
<td>e(股票),f(基金),repo(国债),IDX(指数),ehk(纯港股),ehksz(港股通（深）),ehksh(港股通（沪）)</td>
<td>—</td>
<td>多种类型的，通过&quot;,&quot;分割开</td>
</tr>
<tr>
<td>stockFullname</td>
<td>是否显示股票代码后面的英文 (如: 000001.sz的&quot;.sz&quot;, 默认不显示.sz)</td>
<td>[Array, String]</td>
<td>—</td>
<td>—</td>
<td>默认情况下，选中股票后，输入框中只会出现股票Id, 不会加后缀，如需全部加后缀，可以传“all”, 如果港股加后缀的话，可以传 ['ehksz', 'ehksh']</td>
</tr>
<tr>
<td>hasLoadingIcon</td>
<td>是否需要转圈</td>
<td>Boolean</td>
<td>—</td>
<td>true</td>
<td>—</td>
</tr>
<tr>
<td>curMarket</td>
<td>当前所处的市场</td>
<td>String</td>
<td>—</td>
<td>—</td>
<td>目前的逻辑是。当不是A股的时候，我们不需要处理新股</td>
</tr>
<tr>
<td>stockInit</td>
<td>初始化股票</td>
<td>Object</td>
<td>—</td>
<td>—</td>
<td>—</td>
</tr>
</tbody>
</table>
<h2>stockSearch Events</h2>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
</tr>
</thead>
<tbody>
<tr>
<td>stock_id</td>
<td>点击股票后像上传递的股票ID</td>
<td>—</td>
</tr>
<tr>
<td>stock_name</td>
<td>点击股票后像上传递的股票Name</td>
<td>—</td>
</tr>
</tbody>
</table>
</section></template>

<script>
  export default {
    data() {
      return {
        stockId: '',
        stockName: ''
      }
    },

    methods: {
      chioceStockId(id) {
        this.stockId = id;
      },
      chioceStockName(name) {
        this.stockName = name;
      }
    }
  }
</script>


