<template><section><h1>TabTop</h1>
<p>子页面tab菜单切换组件</p>
<hr>
<h3>基础用法</h3>

<div class="demo-block">
  <tab-top v-on:change="chgMenu" :options="options" :value="value"></tab-top>
</div>
<demo-block>
                        <div slot="desc"><html><head>

</head><body><div class="demo-block">
  <tab-top v-on:change="chgMenu" :options="options" :value="value"></tab-top>
</div>
</body></html></div>
