<h3>Attributes</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>options</td>
<td>tab菜单选项。形如：[{label: '菜单标题', value: '该标题的value值，唯一', visible: '是否展示'}]</td>
<td>Array</td>
<td>必填</td>
<td>-</td>
</tr>
<tr>
<td>value</td>
<td>选中值</td>
<td>String</td>
<td>—</td>
<td>''</td>
</tr>
</tbody>
</table>
<h3>Slot</h3>
<p>无</p>
<h3>Events</h3>
<table class="table"><thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>change</td>
<td>用户点击修改后的回调</td>
<td>value，与入参options数组中某一项的value值匹配，表示选中该菜单项</td>
</tr>
</tbody>
</table>
</section></template>

<script>
  export default {
    data() {
      return {
        options: [{
          label:'待审批指令',
          value:'wait',
          visible:true,
        },{
          label:'已审批指令',
          value:'done',
          visible:true,
        },{
          label:'审批数据校验',
          value:'validation',
          visible:true,
        }],
        value: 'validation'
      }
    },
    methods: {
      chgMenu: function(value){
        console.log(value)
      }
    }
  };
</script>


