</div></demo-block>
<h3>Form Attributes</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>model</td>
<td>表单数据对象</td>
<td>object</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>rules</td>
<td>表单验证规则</td>
<td>object</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>disabled</td>
<td>是否禁用状态</td>
<td>boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>label-width</td>
<td>表单域标签的宽度，作为 Form 直接子元素的 form-item 会继承该值</td>
<td>string</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>disabled</td>
<td>是否禁用状态</td>
<td>boolean</td>
<td>—</td>
<td>false</td>
</tr>
</tbody>
</table>
<h3>Form Methods</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
</tr>
</thead>
<tbody>
<tr>
<td>validate</td>
<td>对整个表单进行校验的方法。若不传入回调函数，则会返回一个 promise</td>
<td>Function(callback: Function(boolean))</td>
</tr>
</tbody>
</table>
<h3>Form-Item Attributes</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>prop</td>
<td>表单域 model 字段，在使用 validate、resetFields 方法的情况下，该属性是必填的</td>
<td>string</td>
<td>传入 Form 组件的 model 中的字段</td>
<td>—</td>
</tr>
<tr>
<td>label</td>
<td>标签文本</td>
<td>string</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>label-width</td>
<td>表单域标签的的宽度，例如 '50px'</td>
<td>string</td>
<td>—</td>
<td>—</td>
</tr>
</tbody>
</table>
</section></template>

<script>
    export default {
        data() {
            return {
                form: {
                    name: '',
                    fundName: '',
                    file: '',
                    type: 'a',
                    desc: '',
                    fund: []
                },
                rules: {
                    name: [
                        { required: true, message: '股票代码不能为空', trigger: 'blur' },
                        { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
                    ],
                    fundName: [
                        { required: true, message: '股票池名称不能为空', trigger: 'blur' },
                    ]
                }
            }
        },

        watch: {
            form: {
                handler: function (val, oldVal) { 
                    //console.log(JSON.stringify(val))
                },
                deep: true
            }
        },

        methods: {
            submitForm(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        alert('submit!');
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            }
        }
    };
</script>


