<template><section>
<h1>loading 加载中</h1>
<hr>
<h2>用法</h2>
<blockquote>
<p>在入口文件中加载 Loading ，并赋值给 Vue 的原型。然后在此单页应用中，就可以使用 <strong>this.$loading.show()</strong> 来显示。 <strong>this.$loading.hide()</strong> 来隐藏</p>
</blockquote>
<demo-block>
                        <div slot="desc"><html><head></head><body>import Vue from 'vue'
import Loading from '../packages/loading/index.js'

Vue.prototype.$loading = Loading

new Vue({
  el: '#app',
  template: '<app>',
  components: { App }
})

</app></body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html">import Vue from 'vue'
import Loading from '../packages/loading/index.js'

Vue.prototype.$loading = Loading

new Vue({
  el: '#app',
  template: '<span class="hljs-tag">&lt;<span class="hljs-name">App</span>/&gt;</span>',
  components: { App }
})

</code></pre>
</div></demo-block>
<h2>基本用法</h2>
<div class="demo-block">
  <xc-button type="text" @click="openLoading_1">点击显示 loading</xc-button>
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><xc-button type="text" @click="openLoading_1">点击显示 loading</xc-button>

  
</body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html">  <span class="hljs-tag">&lt;<span class="hljs-name">xc-button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"text"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"openLoading_1"</span>&gt;</span>点击显示 loading<span class="hljs-tag">&lt;/<span class="hljs-name">xc-button</span>&gt;</span>

  <span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
      <span class="hljs-attr">methods</span>: {
        openLoading_1() {
          <span class="hljs-keyword">this</span>.$loading.show({
            <span class="hljs-attr">text</span>: <span class="hljs-string">'hhh'</span>,
          });
          setTimeout(<span class="hljs-function"><span class="hljs-params">()</span> =&gt;</span> {
            <span class="hljs-keyword">this</span>.$loading.hide();
          }, <span class="hljs-number">1000</span>)
        }
      }
    }
  </span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2>默认文字 - 加载中...</h2>
<div class="demo-block">
  <xc-button type="text" @click="openLoading_2">点击显示 loading</xc-button>
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><xc-button type="text" @click="openLoading_2">点击显示 loading</xc-button>

  
</body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html">  <span class="hljs-tag">&lt;<span class="hljs-name">xc-button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"text"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"openLoading_2"</span>&gt;</span>点击显示 loading<span class="hljs-tag">&lt;/<span class="hljs-name">xc-button</span>&gt;</span>

  <span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
      <span class="hljs-attr">methods</span>: {
        openLoading_2() {
          <span class="hljs-keyword">this</span>.$loading.show();
          setTimeout(<span class="hljs-function"><span class="hljs-params">()</span> =&gt;</span> {
            <span class="hljs-keyword">this</span>.$loading.hide();
          }, <span class="hljs-number">1000</span>)
        }
      }
    }
  </span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2>不要文字</h2>
<div class="demo-block">
  <xc-button type="text" @click="openLoading_3">点击显示 loading</xc-button>
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><xc-button type="text" @click="openLoading_3">点击显示 loading</xc-button>

  
</body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html">  <span class="hljs-tag">&lt;<span class="hljs-name">xc-button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"text"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"openLoading_3"</span>&gt;</span>点击显示 loading<span class="hljs-tag">&lt;/<span class="hljs-name">xc-button</span>&gt;</span>

  <span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
      <span class="hljs-attr">methods</span>: {
        openLoading_3() {
          <span class="hljs-keyword">this</span>.$loading.show(<span class="hljs-string">''</span>);
          setTimeout(<span class="hljs-function"><span class="hljs-params">()</span> =&gt;</span> {
            <span class="hljs-keyword">this</span>.$loading.hide();
          }, <span class="hljs-number">1000</span>)
        }
      }
    }
  </span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2>显示前，与隐藏后</h2>
<div class="demo-block">
  <xc-button type="text" @click="openLoading_4">点击显示 loading</xc-button>
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><xc-button type="text" @click="openLoading_4">点击显示 loading</xc-button>

  
</body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html">  <span class="hljs-tag">&lt;<span class="hljs-name">xc-button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"text"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"openLoading_4"</span>&gt;</span>点击显示 loading<span class="hljs-tag">&lt;/<span class="hljs-name">xc-button</span>&gt;</span>

  <span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
      <span class="hljs-attr">methods</span>: {
        openLoading_4() {
          <span class="hljs-keyword">this</span>.$loading.show({
            <span class="hljs-attr">text</span>: <span class="hljs-string">'哈哈哈哈'</span>,
            onShow() {
              <span class="hljs-built_in">console</span>.log(<span class="hljs-string">'显示前'</span>);
            },
            onHide() {
              <span class="hljs-built_in">console</span>.log(<span class="hljs-string">'隐藏后'</span>);
            }
          });
          setTimeout(<span class="hljs-function"><span class="hljs-params">()</span> =&gt;</span> {
            <span class="hljs-keyword">this</span>.$loading.hide();
          }, <span class="hljs-number">1000</span>)
        }
      }
    }
  </span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2>主要提供3个方法</h2>
<ul>
<li>this.$loading.show(options) [显示]</li>
</ul>
<p>options 支持 String 类型 或 Object 类型</p>
<ul>
<li>
<p>this.$loading.hide()  [隐藏]</p>
</li>
<li>
<p>this.$loading.isLoading()   [判断是否显示]</p>
</li>
</ul>
</section></template>

<script>
  export default {
    methods: {
      openLoading_1() {
        this.$loading.show({
          text: 'hhh',
        });
        setTimeout(() => {
          this.$loading.hide();
        }, 1000)
      },
      openLoading_2() {
        this.$loading.show();
        setTimeout(() => {
          this.$loading.hide();
        }, 1000)
      },
      openLoading_3() {
        this.$loading.show('');
        setTimeout(() => {
          this.$loading.hide();
        }, 1000)
      },
      openLoading_4() {
        this.$loading.show({
          text: '哈哈哈哈',
          onShow() {
            console.log('显示前');
          },
          onHide() {
            console.log('隐藏后');
          }
        });
        setTimeout(() => {
          this.$loading.hide();
        }, 1000)
      }
    }
  }
</script>


