                    label: '招商金证'
                }, {
                    value: '选项5',
                    label: 'BMS'
                }, {
                    value: '选项6',
                    label: 'OMS'
                }, {
                    value: '选项7',
                    label: 'AMS'
                }, {
                    value: '选项8',
                    label: 'CMS'
                }, {
                    value: '选项9',
                    label: 'DMS'
                }],
                value: '',
                value2: '',
                value3: ''
            }
        },

        methods: {
            clear() {
                console.log('已清空')
            },
            handleBlur() {
                console.log(this.value3)
            }
        }
    }
</script>


